import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgForm , FormControl,FormGroup} from '@angular/forms';

@Component({
  selector: 'app-vehicle-registration',
  templateUrl: './vehicle-registration.component.html',
  styleUrls: ['./vehicle-registration.component.css']
})
export class VehicleRegistrationComponent  {
 @ViewChild('f', { static: true }) vehicleregiForm : NgForm
 
  isOldNumberFormat = false;isNewNumberFormat=true;isRemark=false;numberFormatFlag='New';blockUnblockflag='False';
  constructor() { }

  ngOnInit() {
  }

  hideModal():void {document.getElementById('myModal').click();}// for hide popUp
  onSubmit(form:NgForm) {
    alert('Ok')
    console.log(form.value);
   console.log(form)
  }
  onEdit() {
    this.vehicleregiForm.form.reset()
    this.vehicleregiForm.form.setValue({
      'blockUnblock':'True'
    })
    this.numberFormatFlag='New';this.blockUnblockflag='False';
    // .patchValue ({
    //   'MobileNo' : 9923833608,
    //   'blockUnblock':'True'
    // })
  }
  VehicleNumber(id: string){if(id==='Old') {
    this.isOldNumberFormat = true ;this.isNewNumberFormat=false
  }else{this.isOldNumberFormat = false ;this.isNewNumberFormat=true;}}
  blockToggle() {this.isRemark=!this.isRemark;}//for block/Unblock remark hide
}

