import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { AfterloginService } from 'src/app/_services/afterlogin.service';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  isUserlogedin:boolean=false;
  constructor(private _auth:AfterloginService,private route: ActivatedRoute, private router: Router,) { }

  ngOnInit() {
   if(localStorage.length){this.isUserlogedin=true}
         this._auth.isUserlogedincheck.subscribe(dt => {
        this.isUserlogedin=dt
      });
  }

  logout(){
    localStorage.clear();
    this.router.navigate(['']);
  }
  

}
