import { NgModule, OnInit } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TrackingComponent } from './Map/tracking/tracking.component';
import { VehicleRegistrationComponent } from './Master/vehicle-registration/vehicle-registration.component';
import { AfterloginService } from './_services/afterlogin.service';

const routes: Routes = [
  {path: '',loadChildren: () => import('./_userlogin/login.module').then(mod => mod.LoginModule)},
  {path: 'dashboard',loadChildren: () => import('./dashboard/dashboard.module').then(mod => mod.DashboardModule)},
  {path: 'Map/map',loadChildren: () => import('./Map/map/map.module').then(mod => mod.MapModule)},
  {path: 'Master/vehicleOwnerRegistration', loadChildren: () => import('./Master/owner-registration/owner-regi.module').then(mod => mod.OwnerRegiModule)},
  
  {path: 'Map/tracking' , component: TrackingComponent},
  {path: 'Master/vehicleRegistration' , component: VehicleRegistrationComponent},
  {path: '**' , redirectTo:''},


];

@NgModule({
  imports: [RouterModule.forRoot(routes,
    // { enableTracing: true }
    )],
  exports: [RouterModule]
})
export class AppRoutingModule implements OnInit{ 
  constructor(private islogedinService:AfterloginService){}
  ngOnInit(){
    debugger
    this.islogedinService.isUserlogedincheck.next(true)
  };
}
